import Vue from 'vue'
import Resource from 'vue-resource'
import urls from './xhrUrls'

Vue.use(Resource)
/**
 * 配置跨域
 */
Vue.http.interceptors.push((request, next) => {
  request.credentials = true
  next()
})

const getData = (urlKey, body) => {
  return new Promise((resolve, reject) => {
    let urlObj = urls[urlKey]
    let url = urlObj.url
    // let url = `http://localhost:4000${urlObj.url}`
    if (urlObj.static) {
      url = urlObj.url
    }
    let type = urlObj.type || 'post'
    let options = {}
    if (urlObj.bodyType === 'form') {
      options.emulateJSON = true
    }
    if (type === 'get') {
      options.params = body
    }
    Vue.http[type](url, type === 'get' ? options : body, options).then(res => {
      if (res.data.code === 200) {
        resolve(res)
      } else {
        reject(res)
      }
    }, res => {
      reject(res)
    })
  })
}

export default getData



