import Vue from 'vue'
import Router from 'vue-router'
import { getUser } from './data'

Vue.use(Router)

const routerMap = new Router({
  routes: [
    {
      path: '/index',
      component: resolve => require(['../index.vue'], resolve)
    },
    {
      path: '/app',
      component: resolve => require(['../page/app.vue'], resolve),
      children: [
        {
          path: 'project',
          name: 'project',
          component: resolve => require(['../page/project.vue'], resolve)
        },
        {
          path: 'workbench',
          name: 'workbench',
          component: resolve => require(['../page/workbench.vue'], resolve)
        },
        {
          path: 'team',
          name: 'team',
          component: resolve => require(['../page/team.vue'], resolve)
        },
        {
          path: 'teamInfo/:id',
          name: 'team',
          component: resolve => require(['../page/teamInfo.vue'], resolve)
        },
        {
          path: 'doc',
          name: 'doc',
          component: resolve => require(['../page/project.vue'], resolve)
        },
        {
          path: 'lang',
          name: 'lang',
          component: resolve => require(['../page/project.vue'], resolve)
        },
        {
          path: 'user',
          name: 'user',
          component: resolve => require(['../page/user.vue'], resolve)
        }
      ]
    }
  ]
})

routerMap.beforeEach((to, from, next) => {
  getUser().then(() => {
    switch (to.fullPath) {
      case '/':
        next('/index')
        break
      default:
        next()
        break
    }
  }, () => {
    switch (to.fullPath) {
      case '/index':
        next()
        break
      default:
        next('/index')
        break
    }
  })
})

export default routerMap



