import getData from './getData'

let isLoadNewMember = true
let user
let isOk = false

export const getUser = () => {
  return new Promise((resolve, reject) => {
    if (isLoadNewMember) {
      getData('getUser').then(res => {
        isLoadNewMember = false
        user = res.data.data
        isOk = true
        resolve(user)
      }, res => {
        isLoadNewMember = false
        user = null
        reject(res)
      })
    } else {
      isOk ? resolve(user) : reject()
    }
  })
}

export const setNew = () => {
  isLoadNewMember = true
}



